<template>
	<div v-if="chapter.id">
		<nav id="it-nav-viewer-top" class="uk-navbar-container uk-position-top uk-position-fixed uk-light uk-hidden@m" uk-navbar>
			<div class="uk-navbar-left">
				<router-link :to="back" class="uk-navbar-item"><span uk-icon="arrow-left"></span></router-link>
				<div class="uk-navbar-item">{{chapter.series.Title}} | {{chapter.name}}</div>
			</div>

			<div class="uk-navbar-right">
				<ul class="uk-navbar-nav">
					<li>
						<a href="#modal-share" uk-toggle>
							<span uk-icon="social"></span>
						</a>
					</li>
				</ul>
			</div>
		</nav>

		<nav id="it-nav-viewer-bottom" class="uk-navbar-container uk-hidden@m uk-position-bottom uk-position-fixed uk-light" uk-navbar>
			<div class="uk-navbar-center">
				<ul class="uk-navbar-nav" v-if="chapter">
					<li v-if="!previous"><a href="#" class="uk-disabled"><span uk-icon="chevron-left"></span></a></li>
					<li v-else><router-link :to="previous"><span uk-icon="chevron-left"></span></router-link></li>
					<li><a href="#" class="uk-disabled">{{ chapter_page.page_number }}</a></li>
					<li v-if="!next"><a href="#" class="uk-disabled"><span uk-icon="chevron-right"></span></a></li>
					<li v-else><router-link :to="next"><span uk-icon="chevron-right"></span></router-link></li>
				</ul>
			</div>
		</nav>
	</div>
</template>

<script>
	export default {
		name: "NavPagesMobile",
		props: {
			chapter_page: Object,
			chapter: Object
		},
		data() {
			return {
				isActive: false
			}
		},
		computed:{
			back() {
				if (this.chapter.series.has_chapter) {
					//return '/serie/'+ this.chapter.series.id +'/chapter/' + this.chapter.id
					return '/serie/'+ this.chapter.series.id
				}
				else {
					return '/serie/'+ this.chapter.series.id
				}
			},
			next(){
				let index = this.currentIndex();
				if (this.maxPages() > index + 1 ) {
					return this.urlFor(index + 1);
				}
				return null;
			},
			previous(){
				let index = this.currentIndex();
				if (index > 0) {
					return this.urlFor(index - 1);
				}
				return null;
			}
		},
		methods:{
			maxPages(){
				return this.chapter.chapter_pages.length;
			},
			currentIndex(){
				return this.chapter.chapter_pages.indexOf(this.chapter_page);
			},
			urlFor(index) {
				return { path: '#it-page-' + this.chapter.chapter_pages[index]?.page_number };
			}
		}
	};
</script>
<style type="text/css">

</style>