<template>
	<div v-if="chapter" id="it-viewer"> 
		<vue-headful :title="chapter.series.Title + ', ' + chapter.name + ' - Ink&Tales'" />
		
		<NavPagesMobile :chapter="chapter" :chapter_page="chapter_page" />
		
		<div class="it-viewer-mobile uk-background-muted uk-text-center uk-hidden@m">
			<figure id="it-viewer-chapter-cover" class="uk-margin-small-top">
				<img :src="chapter.cover.url" :alt="chapter.name" v-if="chapter.cover" />
				<img src="@/assets/img/placeholder.png" :alt="chapter.name" v-else/>
			</figure>

			<figure :id="'it-page-' + chapter_pages.page_number" v-for="chapter_pages in chapter.chapter_pages" :key="chapter_pages.id" class="uk-margin-small-top">
				<img :src="chapter_pages.page_file.url" :alt="chapter.name" v-if="chapter.cover"/>
				<img src="@/assets/img/placeholder.png" :alt="chapter.name" v-else/>
			</figure>

			<figure id="it-viewer-chapter-last" class="uk-margin-small-top">
				<div class=" uk-card uk-card-default uk-card-body">
					<h3>Publicidad Final</h3>
					<p>
						Lorem ipsum dolor sit amet, consectetur adipisicing elit, sed do eiusmod
						tempor incididunt ut labore et dolorea.
					</p>
				</div>
			</figure>
		</div>
		
		<div class="it-viewer-desktop uk-background-muted uk-text-center uk-visible@m">
			<div uk-grid class="uk-grid-collapse">
				<div class="uk-width-2-3 uk-width-3-4@l uk-background-secondary uk-panel-scrollable uk-inline">
					<NavPages :chapter="chapter" :chapter_page="chapter_page" />
					<img :src="url" v-bind:class="classes" class="uk-border-rounded" />
				</div>

				<aside class="uk-width-1-3 uk-width-1-4@l uk-text-left  uk-panel-scrollable">
					<div class="uk-padding-small">
						<article class="uk-margin-large-bottom">
							<h2 class="uk-margin-remove-bottom">{{chapter.series.Title}}</h2>
							<p class="uk-margin-remove-top">{{chapter.series.Description}}</p>
							<h4 class="uk-margin-remove-bottom">{{'CHAPTERS' | translate}}</h4>
							<ul class="uk-list uk-list-divider uk-margin-small-top">
								<li v-for="chapter in chapter.series.chapters" :key="chapter.id" >
									<div class="uk-text-small">{{'CHAPTER' | translate}} {{chapter.number}}</div>
									<div>{{chapter.name}}</div>
								</li>
							</ul>
						</article>

						<Disqus v-if="disqus_url" shortname="inkandtales" :identifier="disqus_id" :url="disqus_url" />
					</div>
				</aside>
			</div>
		</div>

		<Share :serie="serie" />
	</div>
	<Loading v-else/>
</template>

<script>
	import { Disqus } from 'vue-disqus'
	import NavPages from "../components/NavPages.vue";
	import Loading from "../components/Loading.vue";
	import Share from "../components/Share.vue";
	import NavPagesMobile from "../components/NavPagesMobile.vue";
	import { mapGetters } from 'vuex';
	import { VIEWER_SINGLE_CHAPTER } from "../constants/graphql";

	export default {
		data() {
			return {
				api_url: process.env.VUE_APP_STRAPI_API_URL,
				chapter: null,
				chapterId: this.$route.params.serie,
				chapterPageId: this.$route.params.chapter,

				disqus_id: null,
				disqus_url: null,
			};
		},
		computed: {
			...mapGetters('viewer',[
				'currentClass'
				]),
			classes(){
				return this.currentClass;
			},
			chapter_page(){
				return this.chapter.chapter_pages.filter(x => x.id == this.chapterPageId)[0];
			},
			url(){
				const chapter_page = this.chapter.chapter_pages.filter(x => x.id == this.chapterPageId)[0];
				if (chapter_page){
					//return process.env.VUE_APP_STRAPI_API_URL + chapter_page.page_file.url
					return chapter_page.page_file.url
				}else{
					return '';
				}
			},
		},
		mounted () {
			document.body.classList.add('it-viewer');
			setTimeout(() => {
				this.disqus_id= this.$route.params.chapter;
				this.disqus_url= process.env.VUE_APP_URL + "/viewer/" + this.$route.params.serie + "/" + this.$route.params.chapter;
			}, 100);
		},
		destroyed () {
			document.body.classList.remove('it-viewer')
		},
		components: {
			Disqus,
			Loading,
			NavPages,
			Share,
			NavPagesMobile
		},
		apollo: {
			chapter: {
				query: VIEWER_SINGLE_CHAPTER,
				variables() {
					return {
						id: this.chapterId
					};
				},
				skip () {
					console.log('this.chapterId', this.chapterId);
					return !this.chapterId
				},
			},			
		}
	}
</script>