<template>
	<div v-if="chapter.id" uk-sticky="sel-target: .uk-navbar-container; cls-active: uk-navbar-sticky">
		<nav id="it-nav-floating" class="uk-light uk-visible@m uk-position-bottom uk-position-fixed" uk-navbar>
			<div class="uk-navbar-center">
			<ul class="uk-navbar-nav" v-if="chapter">
				<li>
					<router-link :to="back" class="uk-navbar-item uk-logo">
						{{'BACK' | translate}}
					</router-link>
				</li>
				<li>
					<a href="#" @click="viewFit('fit-height')" :title="'FIT_HEIGHT' | translate">↕</a>
				</li>
				<li>
					<a href="#" @click="viewFit('fit-width')" :title="'FIT_WIDTH' | translate">↔</a>
				</li>

				<li class="uk-visible@l" v-if="!isFullscreen">
					<a href="#" @click="go_full_screen" :title="'FULLSCREEN' | translate"><span uk-icon="expand"></span></a>
				</li>
				<li class="uk-visible@l" v-if="isFullscreen">
					<a href="#" @click="close_full_screen" :title="'FULLSCREEN_EXIT' | translate"><span uk-icon="expand"></span></a>
				</li>
			</ul>
			</div>
		</nav>
		
		<nav id="it-nav-viewer">
			<a class="uk-position-left uk-overlay uk-disabled " v-if="!previous"></a>
			<router-link :to="previous" class="uk-position-left uk-overlay" v-else>
				<span uk-icon="chevron-left"></span>
			</router-link>

			<a class="uk-position-right uk-overlay uk-disabled" v-if="!next"></a>
			<router-link :to="next" class="uk-position-right uk-overlay" v-else>
				<span uk-icon="chevron-right"></span>
			</router-link>
		</nav>		
	</div>
</template>

<script>
	import { mapMutations } from 'vuex';
	export default {
		name: "NavPages",
		props: {
			chapter_page: Object,
			chapter: Object
		},
		data() {
			return {
				isActive: false,
				isFullscreen: false,
			}
		},
		computed:{
			back() {
				/*if (this.chapter.series.has_chapter) {
					return '/serie/'+ this.chapter.series.id +'/chapter/' + this.chapter.id
				}
				else {
					return '/serie/'+ this.chapter.series.id
				}*/
				return '/serie/'+ this.chapter.series.id
			},
			next(){
				let index = this.currentIndex();
				if (this.maxPages() > index + 1 ) {
					return this.urlFor(index + 1);
				}
				return null;
			},
			previous(){
				let index = this.currentIndex();
				if (index > 0) {
					return this.urlFor(index - 1);
				}
				return null;
			}
		},
		methods:{
			...mapMutations('viewer',[
				'setFitClass'
				]),
			maxPages(){
				return this.chapter.chapter_pages.length;
			},
			currentIndex(){
				return this.chapter.chapter_pages.indexOf(this.chapter_page);
			},
			urlFor(index) {
				return { path: '/viewer/' + this.chapter.id + '/' + this.chapter.chapter_pages[index]?.id };
			},
			viewFit (value) {
				this.setFitClass(value);
			},
			go_full_screen(){
				this.isFullscreen = true;
				var elem = document.documentElement;
				if (elem.requestFullscreen) {
					elem.requestFullscreen();
				} else if (elem.msRequestFullscreen) {
					elem.msRequestFullscreen();
				} else if (elem.mozRequestFullScreen) {
					elem.mozRequestFullScreen();
				} else if (elem.webkitRequestFullscreen) {
					elem.webkitRequestFullscreen();
				}
			},
			close_full_screen(){
				this.isFullscreen = false;
				if (document.exitFullscreen) {
					document.exitFullscreen();
				} else if (document.mozCancelFullScreen) {
					document.mozCancelFullScreen();
				} else if (document.webkitCancelFullScreen) {
					document.webkitCancelFullScreen();
				}
			}
		}
	};
</script>
